/** @jsx jsx */
import React, {useEffect, useState} from "react";
import PostLink from "../website-links/post-link";
import LogoLink from "../website-links/logo-link";
import {ThemeSelector} from "../theme-selector-button";
import {jsx} from "theme-ui";


const HeaderInner = ({children, title, ...props}) => {
    return <header style={{
        position: 'sticky',
        top: 0,
        zIndex: 5,
    }}>
            <div>
                <div>
                    <LogoLink/>
                </div>
                <div>
                    {children}
                </div>
                <div>
                    <ThemeSelector/>
                </div>
            </div>
    </header>
}

const Header = (props) => {
    const [hasMounted, setHasMounted] = useState(false);
    useEffect(() => {
        setHasMounted(true);
    }, [])

    if (!hasMounted) {
        return null;
    }

    const headerLinks = [
        <PostLink/>
    ];

    return <HeaderInner {...props}>
        {headerLinks.map((link, index) => {
            if (headerLinks[index + 1]) {
                return <div key={index} style={{
                    paddingRight: '26px',
                }}>
                    <div key={index}>
                        {link}
                    </div>
                </div>
            }
            return link;
        })}
    </HeaderInner>

}

export default Header
